import React, { Component } from 'react';
import $ from 'jquery';
import SearchResults from './SearchResult.js';
import {Row, Col} from 'react-bootstrap';
class AppFrame extends Component {
    constructor(props) {
      super(props);
      this.state = {tags: '', showRes: false};
      this.handleChange = this.handleChange.bind(this);
      this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleChange(event) {
      this.setState({tags: event.target.value});
    }

    handleSubmit(event) {
      console.log('A name was submitted: ' + this.state.tags);
      this.setState({showRes: true});
      $.ajax({
        url: "http://192.168.1.69:9000/image/tags", // Url to which the request is send
        type: "GET",             // Type of request to be send, called as method
        crossDomain: true,
        dataType: "json",
        data: {
          id: this.state.tags
        }, // Data sent to server, a set of key/value pairs (i.e. form fields and values)
        success: function(data){   // A function to be called if request succeeds
          console.log(data);
        }
      });
      event.preventDefault();
    }
    render() {
        let appDesc;
        if (!this.state.showRes) {
          appDesc=(
            <h3 className="img-search-desc">Enter a description of your image and we will find it for you</h3>
          )
        }
        else {
          appDesc=(
            <div></div>
          )
        }
        return(
          <div>
            <Row className="show-grid">
              <Col xs={3} md={2}></Col>
              <Col xs={8} md={8}>
                <div className="home-search-cont">
                <Row className="show-grid">
                <Col xs={3} md={2}></Col>
                <Col xs={8} md={8}>
                  <div className="search-desc">
                    <h1></h1>
                    <h1>Whimsy</h1>
                    {appDesc}
                  </div>
                </Col>
                <Col xs={3} md={2}></Col>
                </Row>
                <Row className="show-grid">
                  <Col xs={3} md={2}></Col>
                  <Col xs={8} md={8}>
                  <form onSubmit={this.handleSubmit}>
                      <input type="text" name="id" className="form-control" value={this.state.tags} onChange={this.handleChange} placeholder="Search" />
                  </form>
                  </Col>
                  <Col xs={3} md={2}></Col>
                </Row>
                <Row className="show-grid">
                  <Col xs={3} md={2}></Col>
                  <Col xs={8} md={8}>
                    <SearchResults shown={this.state.showRes} tag={this.state.tags}/>
                  </Col>
                  <Col xs={3} md={2}></Col>
                </Row>
                </div>
              </Col>
              <Col xs={3} md={2}>
              </Col>
            </Row>
          </div>
        );
    }
};

export default AppFrame;



