import React from 'react';
import ImageItem from './imageitem.js';
import {Row} from 'react-bootstrap';

var images = [
  {
    "tag":"lunch",
    "path":"http://tastesbetterfromscratch.com/wp-content/uploads/2015/01/Italian_Grilled_Cheese4.jpg"
  },
  {
    "tag":"lunch",
    "path":"http://tastesbetterfromscratch.com/wp-content/uploads/2015/01/Italian_Grilled_Cheese4.jpg"
  },
  {
    "tag":"dinner",
    "path":"https://4.bp.blogspot.com/-g3zglCe_5ec/VsDwHYlf1WI/AAAAAAAALqc/6okQoTrF7EI/s1600/12744322_615377968612526_2374576786353097122_n.jpg"
  },
  {
    "tag":"lunch",
    "path":"http://tastesbetterfromscratch.com/wp-content/uploads/2015/01/Italian_Grilled_Cheese4.jpg"
  },
  {
    "tag":"dinner",
    "path":"https://4.bp.blogspot.com/-g3zglCe_5ec/VsDwHYlf1WI/AAAAAAAALqc/6okQoTrF7EI/s1600/12744322_615377968612526_2374576786353097122_n.jpg"
  },
  {
    "tag":"lunch",
    "path":"http://tastesbetterfromscratch.com/wp-content/uploads/2015/01/Italian_Grilled_Cheese4.jpg"
  },
  {
    "tag":"dinner",
    "path":"https://4.bp.blogspot.com/-g3zglCe_5ec/VsDwHYlf1WI/AAAAAAAALqc/6okQoTrF7EI/s1600/12744322_615377968612526_2374576786353097122_n.jpg"
  },
  {
    "tag":"breakfast",
    "path":"http://cdn.ruled.me/wp-content/uploads/2014/12/buffalochickenjalapenopoppercasserole1.jpg"
  }
];
class SearchResults extends React.Component {
  render() {
    var imgs = images;
    var tags = this.props.tag;
    var targetImgs = [];
    var test = '';
    for (var i = 0; i < imgs.length; i++) {
      var tagString = tags.split(" ");
      for (var j = 0; j < tagString.length; j++) {
        if (imgs[i].tag === tagString[j]) {
            targetImgs.push(imgs[i]);
        }
      }
    }
    let imagesRes;
    if (this.props.shown) {
      if(targetImgs.length !== 0){
        imagesRes=(
          <Row className="img-row">
            {targetImgs.map(function(img, index){
              return (
                <ImageItem key={index} path={img.path} />
              )
            })}
          </Row>
        )
      }
      else{
        imagesRes=(
          <h1>No matched discription</h1>
        )
      }
    }
    else{
      imagesRes=(<Row></Row>)
    }
    return (
      <div>
        {test}
        {imagesRes}
      </div>
    );
  }
}

export default SearchResults;



