import React, { Component } from 'react';
import {Button, Col} from 'react-bootstrap';
import '../App.css';
class ImageItem extends Component {

  constructor() {
    super();
    this.state = { user: [] };
  }

  render() {
    return (
      <Col md={4} sm={4} xs={4}>
        <Button>
        <img alt="img" src={this.props.path} className="img-thumbnail"/>
        </Button>
      </Col>
     );
  }
}

export default ImageItem;



